it-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n}\ntextarea:focus,\ninput:focus {\n    outline: none;\n}\n\n.input {\n    position: relative;\n    &_white {\n        .input__field {\n            color: $white;\n            border-bottom: 1px solid $white;\n            &::placeholder {\n                color: $white;\n            }\n        }\n    }\n\n    &._form-focus {\n    }\n    &._form-error {\n        .input__field {\n            border-bottom: 1px solid $red;\n        }\n    }\n\n    // .input__field\n    &__field {\n        padding-bottom: 1.4rem;\n        display: block;\n        width: 100%;\n        border-radius: 0 !important;\n        border-bottom: 1px solid $black;\n        font-family: EuclidCircularA;\n        line-height: 1;\n        color: $black;\n        transition: border-bottom 0.3s ease;\n        &::placeholder {\n            color: $black;\n        }\n        @media (max-width: 48em) {\n            padding-bottom: 3rem;\n        }\n    }\n}\n\ntextarea.input {\n    padding: 0;\n    resize: none;\n}\n\n.form-error {\n    position: absolute;\n    top: calc(100% + 0.5rem);\n    left: 0;\n    color: $red;\n    @media (max-width: 48em) {\n        top: calc(100% + 1rem);\n    }\n}\n\n.file-input {\n    display: flex;\n    flex-direction: column;\n    row-gap: 3rem;\n    &_white {\n        .file-input__icon-wrap {\n            background-color: $white;\n        }\n    }\n    @media (min-width: 48em) {\n        flex-direction: row;\n        column-gap: 5.6rem;\n        row-gap: normal;\n    }\n\n    // .file-input__content\n\n    &__content {\n        display: flex;\n        flex-direction: column;\n        row-gap: 0.8rem;\n    }\n\n    // .file-input__content-inner\n\n    &__content-inner {\n        position: relative;\n        display: flex;\n        align-items: center;\n        column-gap: 1.6rem;\n        @media (max-width: 48em) {\n            column-gap: 2.4rem;\n        }\n    }\n\n    // .file-input__icon-wrap\n\n    &__icon-wrap {\n        display: inline-flex;\n        align-items: center;\n        justify-content: center;\n        flex: 0 0 5rem;\n        width: 5rem;\n        height: 5rem;\n        border-radius: 50%;\n        background-color: $black;\n        @media (max-width: 48em) {\n            flex: 0 0 8rem;\n            width: 8rem;\n            height: 8rem;\n        }\n    }\n\n    // .file-input__icon\n\n    &__icon {\n        width: 2.4rem;\n        height: 2.4rem;\n        object-fit: contain;\n        @media (max-width: 48em) {\n            width: 4rem;\n            height: 4rem;\n        }\n    }\n\n    // .file-input__title\n\n    &__title {\n        font-family: 'DrukCyr';\n    }\n\n    // .file-input__input\n\n    &__input {\n        position: absolute;\n        top: 0;\n        left: 0;\n        width: 100%;\n        height: 100%;\n        opacity: 0;\n    }\n\n    // .file-input__text\n\n    &__text {\n        ._error & {\n            color: $red;\n        }\n    }\n\n    // .file-input__info\n\n    &__info {\n        position: relative;\n        padding: 0.6rem;\n        padding-right: 5.6rem;\n        display: flex;\n        align-self: flex-start;\n        column-gap: 1.2rem;\n        width: 26.9rem;\n        border-radius: 1.5rem;\n        background-color: $gray-darken;\n        &:not(._error &, ._filled &) {\n            display: none;\n        }\n        @media (max-width: 48em) {\n            padding: 1.6rem;\n            padding-right: 11.2rem;\n            column-gap: 1.6rem;\n            width: 43.4rem;\n            border-radius: 3rem;\n        }\n    }\n\n    // .file-input__image-container\n\n    &__image-container {\n        position: relative;\n        flex: 0 0 6.4rem;\n        width: 6.4rem;\n        height: 6.4rem;\n        &::after {\n            content: '';\n            position: absolute;\n            bottom: 0.4rem;\n            right: 0.4rem;\n            width: 2rem;\n            height: 2rem;\n            background-image: url(./assets/images/icons/success.svg);\n            background-size: contain;\n            background-repeat: no-repeat;\n            ._error & {\n                background-image: url(./assets/images/icons/error.svg);\n            }\n        }\n        img {\n            display: block;\n            height: 100%;\n            border-radius: 1rem;\n            object-fit: cover;\n        }\n        @media (max-width: 48em) {\n            flex: 0 0 10rem;\n            width: 10rem;\n            height: 10rem;\n            &::before {\n                bottom: 1.2rem;\n                right: 1.2rem;\n                width: 3.2rem;\n                height: 3.2rem;\n            }\n            img {\n                border-radius: 2rem;\n            }\n        }\n    }\n\n    // .file-input__info-text\n\n    &__info-text {\n        display: flex;\n        flex-direction: column;\n    }\n\n    // .file-input__name\n\n    &__name {\n        margin-bottom: 1.2rem;\n        padding-top: 0.6rem;\n        display: flex;\n        color: $white;\n        span {\n            display: block;\n            &:first-child {\n                max-width: 10rem;\n                overflow: hidden;\n                text-overflow: ellipsis;\n                white-space: nowrap;\n            }\n        }\n        @media (max-width: 48em) {\n            margin-bottom: 1.6rem;\n            padding-top: 1rem;\n            span {\n                &:first-child {\n                    max-width: 14rem;\n                }\n            }\n        }\n    }\n\n    // .file-input__size\n\n    &__size {\n        color: rgba(255, 255, 255, 0.6);\n    }\n\n    // .file-input__remove-btn\n\n    &__remove-btn {\n        position: absolute;\n        top: 0.8rem;\n        right: 0.8rem;\n        width: 1.6rem;\n        height: 1.6rem;\n        @media (max-width: 48em) {\n            top: 2rem;\n            right: 2rem;\n            width: 2.8rem;\n            height: 2.8rem;\n        }\n    }\n\n    // .file-in