\n//   <div class=\"tabs__body\">  </div>\n// </div>\n// </div>\n",".accordion {\n    // .accordion__item\n\n    &__item {\n        border-radius: 4rem;\n        color: $black;\n        background-color: $green;\n        &_violet {\n            background-color: $violet;\n        }\n        &_pink {\n            background-color: $pink;\n        }\n    }\n\n    // .accordion__title\n\n    &__title {\n        padding: 3rem 3.2rem 3rem 4rem;\n        display: flex;\n        flex-direction: row-reverse;\n        justify-content: space-between;\n        align-items: center;\n        width: 100%;\n        &._accordion-active {\n            .accordion__title-icon {\n                transform: rotate(-180deg);\n            }\n        }\n        @media (max-width: 48em) {\n            padding: 4rem;\n        }\n    }\n\n    // .accordion__title-icon\n\n    &__title-icon {\n        flex: 0 0 5rem;\n        width: 5rem;\n        height: 5rem;\n        transition: transform 0.3s ease;\n        @media (max-width: 48em) {\n            flex: 0 0 8rem;\n            width: 8rem;\n            height: 8rem;\n        }\n    }\n\n    // .accordion__title-txt\n\n    &__title-txt {\n    }\n\n    // .accordion__body\n\n    &__body {\n        padding: 4rem 3.2rem;\n        padding-top: 0;\n        @media (max-width: 48em) {\n            padding: 4rem;\n            padding-top: 0;\n        }\n    }\n\n    // .accordion__text\n\n    &__text {\n        font-family: EuclidCircularA;\n        max-width: 72.6rem;\n        @media (max-width: 48em) {\n            max-width: 62.2rem;\n        }\n    }\n}\n\n// <div data-accordion data-accordion-one-active class=\"accordion\">\n//           <div class=\"accordion__item\">\n//             <button type=\"button\" data-accordion-item class=\"accordion__title\">\n//               <svg\n//                 class=\"accordion__title-icon\"\n//                 width=\"52\"\n//                 height=\"52\"\n//                 viewBox=\"0 0 52 52\"\n//       